.body-bg {
  z-index: -3;
  background-image: url(../../assets/img/background-stars-mobile.jpeg);
  background-color: #000000;
  background-position: center top;
  background-repeat: no-repeat;
  overflow-y: scroll;
  width: 100%;
  height: 100%; }
  @media (min-width: 768px) {
    .body-bg {
      background-image: url(../../assets/img/background-stars-desktop.jpeg); } }

.main-header {
  width: 100%;
  height: 180px; }
  .main-header .logo {
    max-height: 88px; }

.main-nav {
  --borderColor: yellow;
  flex-direction: column;
  border-top: 1px solid var(--borderColor);
  border-bottom: 1px solid var(--borderColor);
  background: #000000;
  height: 40px;
  padding: 8px auto;
  display: flex;
  align-items: center;
  justify-content: center; }

.main-menu {
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  padding: 1px 0 0 1px;
  font-size: 24px;
  color: var(--borderColor); }
  .main-menu li {
    border-right: 1px solid var(--borderColor);
    border-left: 1px solid var(--borderColor);
    margin-bottom: 2px;
    line-height: 28px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 38px; }
    .main-menu li:first-of-type {
      border-left-width: 2px; }
    .main-menu li:last-of-type {
      border-right-width: 2px; }
    .main-menu li > .link {
      opacity: 1;
      text-decoration: none;
      color: var(--colorBorder);
      transition: opacity 0.3s ease-in-out; }
      .main-menu li > .link:hover {
        opacity: 0.5; }
    .main-menu li.is-active {
      --shadowColor: #3d8cff;
      background-color: #ffffff;
      color: #000000;
      box-shadow: inset 0px 0px 15px 10px var(--shadowColor); }
      .main-menu li.is-active.is-sith {
        --shadowColor: #cf3030; }
      .main-menu li.is-active.is-other {
        --shadowColor: #008a3a; }
      .main-menu li.is-active > .link {
        pointer-events: none; }
        .main-menu li.is-active > .link:hover {
          opacity: 1; }

.image-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr));
  grid-auto-rows: 20rem;
  grid-auto-flow: dense;
  gap: 16px; }
  .image-grid > * {
    display: flex;
    align-items: center;
    justify-content: center; }

.image-block {
  overflow: hidden; }
  .image-block > figure {
    position: relative;
    background: #000000;
    width: 100%;
    height: 100%; }
    .image-block > figure > img {
      position: relative;
      top: 0;
      left: 0;
      opacity: 1;
      width: 100%;
      transition: all 0.5s ease; }
    .image-block > figure > figcaption {
      background: #000000;
      font-size: 24px;
      line-height: 32px;
      transition: bottom 0.5s ease;
      box-shadow: 0 -3px 6px 0 rgba(0, 0, 0, 0.16);
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 32px; }
  @media (min-width: 992px) {
    .image-block > figure > img {
      opacity: 0.5; }
    .image-block > figure > figcaption {
      bottom: -32px; }
    .image-block > figure:hover > img {
      top: -10%;
      left: -10%;
      opacity: 1;
      height: auto;
      width: 120%; }
    .image-block > figure:hover > figcaption {
      bottom: 0; } }

.main-footer {
