.rotate--left {
  transform: rotate(90deg); }

.rotate--right {
  transform: rotate(270deg); }

.click-no {
  cursor: default !important;
  pointer-events: none; }

.click-active {
  cursor: pointer !important; }

.click-move {
  cursor: move !important; }

.click-help {
  cursor: help !important; }

.click-grab {
  cursor: grab !important; }

.click-default {
  cursor: default !important; }

.box-sizing-border {
